import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uploadImages from '../../components/common/upload-images.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue';

let changelook=['所有人可见', '仅自己可见'];
export default {
	components:{
		uniNavBar,
		uploadImages,
		uniPopup
	},
	data() {
		return {
			isget:false,
			yinsi:"所有人可见",
			text:'',
			imglist:[],
			showCusPop: true,
		}
	},
	onBackPress() {
		//如果有评论
		if(!this.text && this.imglist.length<1){return;}
		if(!this.isget){
			this.baocun(); 
			return true;
		}
	},
	methods: {
		
		//保存为草稿
		baocun(){
			uni.showModal({
				content: '是否要存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log('保存')
					}else{
						console.log('不保存')
					}
					this.isget=true;
					uni.navigateBack({
						delta: 1
					})
				},  
			})
		},
		//返回
		back(){
			uni.navigateBack({
				delta: 1
			});
		},
		//发布
		submit(){
			console.log("发布")
		},
		//隐私
		changelook(){
			console.log("隐私")
			uni.showActionSheet({
				itemList:changelook,
				success: (res)=> {
					// console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					this.yinsi=changelook[res.tapIndex];
				}
			});
		},
		upload(arr){
			this.imglist=arr;
			console.log(this.imglist);
		},
		handleCusClick(){
			this.showCusPop = false;
		}
	}
}
